$primaryColor: #4527a4;
$secondaryColor: #8344c5;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

@for $i from 1 through 12 {
	.columns-#{$i} {
		grid-template-columns: repeat(#{$i}, 1fr);
	}
}

@media (max-width: 768px) {
	@for $i from 1 through 12 {
		.columns-tablet-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

@media (max-width: 576px) {
	@for $i from 1 through 12 {
		.columns-mobile-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

@keyframes ifbShine {
	100% {
		left: 125%;
	}
}

// Instagram Feed
.wp-block-ifb-instagram {
	* {
		box-sizing: border-box;
	}

	.ifbInstagramFeed {
		.ifbGallery {
			display: grid;
		}

		.loadMore {
			text-align: center;

			&.hidden {
				display: none;
			}

			button {
				color: #fff;
				background: $primaryColor;
				padding: 10px 20px;
				margin-top: 20px;
				border: none;
				border-radius: 3px;
				transition: all 0.3s ease;
				cursor: pointer;

				&:hover,
				&:focus {
					background-color: $secondaryColor;
					border: none;
				}
			}
		}

		.galleryItem {
			height: 100%;
			position: relative;
			overflow: hidden;

			figcaption {
				p {
					width: max-content;
					max-width: 100%;
					padding: 0 15px;
				}
			}

			&:hover {
				.galleryFigure {
					&.zoomIn {
						img {
							transform: scale(1.1);
						}
					}

					&.zoomOut {
						img {
							transform: scale(1);
						}
					}

					&.rotateIn {
						img {
							transform: scale(1.1) rotate(3deg);
						}
					}

					&.rotateOut {
						img {
							transform: scale(1) rotate(0deg);
						}
					}

					&.shine::before {
						animation: ifbShine .75s;
					}

					figcaption {
						opacity: 1;
					}
				}
			}
		}

		.galleryHeader {
			display: flex;
			align-items: center;
			justify-content: space-between;

			.headerLeft {
				display: flex;
				align-items: center;
			}

			.profileImg {
				min-width: 55px;
				width: 55px;
				height: 55px;
				overflow: hidden;
				box-sizing: border-box;
				margin: 0;
				margin-right: 15px;
				border-radius: 50%;
			}

			.userName {
				font-size: 18px;
				font-weight: bold;
				margin: 0;

				a {
					text-decoration: none;
					color: #4527a4;
				}
			}

			.headerRight {
				margin-left: auto;
			}

			.followBtn {
				text-decoration: none;
				color: #fff;
				background: $primaryColor;
				padding: 10px 20px;
				border-radius: 3px;
				transition: all 0.3s ease;

				&:hover {
					background-color: $secondaryColor;
				}
			}
		}

		.galleryFigure {
			width: 100%;
			height: 100%;
			position: relative;
			margin: 0;
			transition: all 0.4s ease-in-out;

			&.zoomOut {
				img {
					transform: scale(1.1);
				}
			}

			&.rotateOut {
				img {
					transform: scale(1.1) rotate(3deg);
				}
			}

			&.shine {
				position: relative;

				&::before {
					content: '';
					display: block;
					position: absolute;
					top: 0;
					left: -75%;
					z-index: 2;
					width: 50%;
					height: 100%;
					background: linear-gradient(to right, #fff0 0%, #ffffff4d 100%);
					transform: skewX(-25deg);
				}
			}

			figcaption {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				opacity: 0;
				margin: 0;
				transition: all 0.4s ease-in-out;

				p {
					position: absolute;
					margin: 0;
				}

				&.overlayTop p {
					top: 8%;
					left: 50%;
					transform: translate(-50%, 10%);
				}

				&.overlayCenter p {
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
				}

				&.overlayBottom p {
					bottom: 8%;
					left: 50%;
					transform: translate(-50%, 10%);
				}
			}
		}

		img,
		video {
			width: 100%;
			max-width: 100%;
			height: 100%;
			max-height: 100%;
			object-fit: cover;
			transition: all 0.3s cubic-bezier(0.20, 0.60, 0.70, 0.80);
		}

